@import 'vars';

.about-container {
    margin-top: 2rem;
    display: flex;
    justify-content: space-around;
}

.about-img {
    width: 40%;
    object-fit: cover;
}

.about-text {
    width: 40%;
    line-height: 3.5;
    font-size: 1.2rem;
}

h2 {
    font-size: 1.5rem;
    margin-bottom: 6px;
}

p {
    margin-bottom: 1rem;
    line-height: 1.7;
    text-align: justify;
}

@media (max-width: 768px) {

    .about-img {
        order: 0;
        width: 100%;
        height: auto;
        margin-bottom: 1rem;
    }
    
    .about-text {
        order: 1;
        width: 100%;
    }

    .about-container {
        flex-direction: column;
    }

    .about-container {
        margin-left: 2rem;
        margin-right: 2rem;
    }

    .img-cate {
        width: 80%;
        float: none;
        display: block;
        margin: 2rem auto;
    }

    h2 {
        text-align: center;
    }
}


@media (max-width: 450px) {

    .img-cate {
        width: 100%;
    }

}